import React from "react";

const HackAtBrownModal = props => (
	<div>
		<p className="card__modal_title"> Hack@Brown 2019: Recess </p>
		<img src="/HackAtBrown/cover.png" alt="the words Hack@Brown: Recess over a playground on a yellow background"/>

		<p> College hackathons are often prize-driven and competitive environments that can be unwelcoming and uninclusive.  </p>
		<p> As co-directors, <a>Miranda Christ</a> and I wanted to create a different type of environment: one where students and mentors are free to learn, play, and experiment with something new. </p>

	</div>
);

export default HackAtBrownModal;


